@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  font-family: "Roboto", sans-serif;
  text-align: center;
  font-size: calc(0.7rem + 0.5vw);
}

h1 {
  font-size: calc(1.2rem + 0.5vw);
}

.app {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.slider-container > * {
  text-align: center;
  margin: 5px 10px;
  width: 40vw;
}

.canvas-slider {
  display: none;
}

.slider-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.interface {
  font-size: 0.7rem;
  display: grid;
  margin: 1rem;
}

.interface > * {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.interface > *:nth-child(1) {
  grid-area: 1 / 1 / 2 / 2;
}
.interface > *:nth-child(2) {
  grid-area: 2 / 1 / 3 / 2;
}
.interface > *:nth-child(3) {
  grid-area: 3 / 1 / 4 / 2;
}
.interface > *:nth-child(4) {
  grid-area: 1 / 2 / 4 / 3;
}
.interface > *:nth-child(5) {
  grid-area: 4 / 1 / 5 / 3;
}

.svg-button-container {
  display: grid;
  grid-template-rows: repeat(3, auto);
  grid-template-columns: repeat(2, auto);
  justify-content: center;
  align-items: center;
}

.svg-button {
  width: calc(2rem + 1vw);
  height: calc(2rem + 1vw);
  background-color: blue;
  padding: 0.5rem;
  border-radius: 10px;
  margin: 1rem;
  cursor: pointer;
}

.randomize-button {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 2rem auto;
  padding: 1rem;
  background-color: #eee;
  color: #444;
  border-radius: 10px;
  border: 2px solid #444;
  cursor: pointer;
}

@media only screen and (min-width: 900px) {
  .app {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    justify-content: center;
  }

  .interface {
    margin: 2rem;
    text-align: center;
    display: flex;
    flex-direction: column;
  }

  .canvas {
    width: 50vw;
    height: 100vh;
  }

  .slider-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .slider-container > * {
    text-align: center;
    margin: 5px 10px;
    width: 12rem;
  }

  .svg-button-container {
    display: flex;
    flex-direction: row;
  }
}

@media only screen and (min-width: 1400px) {
  * {
    font-size: 1rem;
  }

  .app {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .interface {
    position: absolute;
    top: 0;
    left: 0;
    margin: 2rem;
    text-align: center;
    display: flex;
    flex-direction: column;
  }

  .canvas {
    transform: translateX(12vw);
    width: auto;
    height: auto;
  }

  .canvas-slider {
    display: block;
  }

  .slider-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .slider-container > * {
    text-align: center;
    margin: 5px 10px;
    width: 12rem;
  }

  .svg-button {
    height: 2rem;
    width: 2rem;
    margin: 0.5rem;
  }
}

